export class Media {

    constructor(
        public name: string,
        public description: string,
        public file: File,
        public upgrade_to_1080: boolean,
        public privacy: boolean
    ){}

    public toJSON(): Object {
        return {
            name:                   this.name,
            description:            this.description,
            'privacy.view':        (this.privacy) ? 'nobody' : 'anybody'
        }
    }
}


